
.title {
  flex: 1 1 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 1.5rem;
  font-weight: 600;
  user-select: none;
}

.entity {
  display: flex;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
  border-bottom: 1px solid #e2e6ea;
  padding: 0 2rem;
  user-select: none;
  color: inherit;
  text-decoration: none;
}

.entity:hover {
  background-color: #f8f9fa;
}

.td {
  padding: 0 0.25rem;
  width: 80px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.tdName {
  flex: 1 1 auto;
  margin-left: 1rem;
}

.tdCategory {
  flex: 1 1 auto;
  text-align: right;
  margin-right: 1rem;
}


.presenter {
  width: 80px;
  height: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 2.5rem;
  text-transform: uppercase;
  letter-spacing: .015em;
  user-select: none;
  margin: .5rem 0;
  border-radius: .25rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.empty {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 10rem;
  width: 100%;
  user-select: none;
}
